* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #fef5f8;
  font-family: Bubblegum Sans, ZCOOL QingKe HuangYou, Comic Neue, Nunito, Microsoft YaHei, Arial Rounded MT Bold, Arial, sans-serif;
  line-height: 1.6;
  transition: all .3s;
  overflow-x: hidden;
}

body.pink {
  background-color: #fef5f8;
  background-image: linear-gradient(120deg, #fef5f8 0%, #fde2e7 100%);
}

body.white {
  background-color: #fff;
  background-image: linear-gradient(120deg, #fff 0%, #f8f9fa 100%);
}

body.blue {
  background-color: #f0f9ff;
  background-image: linear-gradient(120deg, #f0f9ff 0%, #e0f2fe 100%);
}

.container {
  backdrop-filter: blur(12px);
  background-color: #fffffff2;
  border-radius: 24px;
  max-width: 1000px;
  margin: 20px auto;
  padding: 30px;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 32px #0000001a, 0 1px 3px #00000014, inset 0 1px #ffffff80;
}

.container:before {
  content: "";
  z-index: 1;
  background: linear-gradient(90deg, #0000, #fffc, #0000);
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

header {
  text-align: center;
  border-bottom: 1px solid #00000014;
  margin-bottom: 40px;
  padding-bottom: 25px;
  position: relative;
}

header:after {
  content: "";
  background: linear-gradient(90deg, #0000, #ff6b9d, #0000);
  border-radius: 2px;
  width: 80px;
  height: 3px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

body.pink header:after {
  background: linear-gradient(90deg, #0000, #ff6b9d, #0000);
}

body.white header:after {
  background: linear-gradient(90deg, #0000, #333, #0000);
}

body.blue header:after {
  background: linear-gradient(90deg, #0000, #4a90e2, #0000);
}

header h1 {
  letter-spacing: 1.2px;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(120deg, #333, #666) text;
  margin-bottom: 30px;
  font-size: 36px;
  font-weight: bold;
  display: inline-block;
  position: relative;
}

header h1:after {
  content: "";
  opacity: 0;
  background: linear-gradient(90deg, #0000, #ff6b9d, #0000);
  border-radius: 1px;
  height: 2px;
  transition: all .3s;
  position: absolute;
  bottom: -8px;
  left: 10%;
  right: 10%;
}

header h1:hover:after {
  opacity: 1;
  bottom: -10px;
}

body.pink header h1 {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 2px 4px #ff6b9d1a;
  background: linear-gradient(120deg, #ff6b9d, #ff85b0) text;
}

body.pink header h1:after {
  background: linear-gradient(90deg, #0000, #ff6b9d, #0000);
}

body.white header h1 {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 2px 4px #0000001a;
  background: linear-gradient(120deg, #333, #666) text;
}

body.white header h1:after {
  background: linear-gradient(90deg, #0000, #333, #0000);
}

body.blue header h1 {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 2px 4px #4a90e21a;
  background: linear-gradient(120deg, #4a90e2, #6aa9f4) text;
}

body.blue header h1:after {
  background: linear-gradient(90deg, #0000, #4a90e2, #0000);
}

.header-actions {
  backdrop-filter: blur(12px);
  background-color: #ffffffe6;
  border-radius: 24px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 25px;
  padding: 25px;
  transition: all .3s;
  display: flex;
  box-shadow: inset 0 1px #fffc, 0 4px 16px #0000001a;
}

.header-actions:hover {
  box-shadow: inset 0 1px #fffc, 0 6px 20px #00000026;
}

.font-selector {
  backdrop-filter: blur(8px);
  background-color: #ffffffe6;
  border-radius: 24px;
  margin-top: 20px;
  padding: 25px;
  transition: all .3s;
  box-shadow: inset 0 1px #ffffff80, 0 4px 16px #0000001a;
}

.font-selector:hover {
  box-shadow: inset 0 1px #fffc, 0 6px 20px #00000026;
}

.font-selector h3 {
  color: #333;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: bold;
  transition: all .3s;
}

#font-select {
  cursor: pointer;
  background-color: #ffffffe6;
  border: 2px solid #0000001a;
  border-radius: 28px;
  padding: 12px 24px;
  font-family: inherit;
  font-size: 16px;
  transition: all .3s;
  box-shadow: 0 2px 8px #0000001a, inset 0 1px #ffffff80;
}

#font-select:hover {
  border-color: #0003;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px #00000026, inset 0 1px #fffc;
}

#font-select:focus {
  border-color: #ff6b9d;
  outline: none;
  box-shadow: 0 4px 12px #00000026, 0 0 0 3px #ff6b9d33, inset 0 1px #fffc;
}

#font-preview {
  background-color: #ffffffb3;
  border: 2px dashed #0000001a;
  border-radius: 20px;
  min-height: 120px;
  padding: 25px;
  font-family: Bubblegum Sans, ZCOOL QingKe HuangYou, Comic Neue, Nunito, Microsoft YaHei, Arial Rounded MT Bold, Arial, sans-serif;
  font-size: 18px;
  line-height: 1.6;
  transition: all .3s;
}

#font-preview:hover {
  background-color: #ffffffe6;
  border-color: #0003;
  box-shadow: inset 0 2px 4px #00000014;
}

.edit-state {
  backdrop-filter: blur(8px);
  background-color: #ffffffe6;
  border-radius: 24px;
  margin-top: 20px;
  padding: 20px;
  transition: all .3s;
  box-shadow: inset 0 1px #ffffff80, 0 4px 16px #0000001a;
}

.edit-state:hover {
  box-shadow: inset 0 1px #fffc, 0 6px 20px #00000026;
}

.edit-state-content {
  flex-wrap: wrap;
  align-items: center;
  gap: 15px;
  margin-bottom: 10px;
  display: flex;
}

.edit-state-content span {
  white-space: nowrap;
  font-weight: 500;
}

#edit-id-input {
  background-color: #ffffffe6;
  border: 2px solid #0000001a;
  border-radius: 24px;
  flex: 1;
  min-width: 200px;
  padding: 12px 20px;
  font-family: inherit;
  font-size: 16px;
  transition: all .3s;
  box-shadow: inset 0 2px 4px #00000014, inset 0 1px #ffffff80;
}

#edit-id-input:focus {
  border-color: #4caf50;
  outline: none;
  transform: translateY(-1px);
  box-shadow: inset 0 2px 4px #0000001a, 0 0 0 3px #4caf5033;
}

#load-edit-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(120deg, #4caf50, #66bb6a);
  border: none;
  border-radius: 28px;
  padding: 12px 28px;
  font-family: inherit;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s;
  box-shadow: 0 4px 12px #4caf504d, inset 0 1px #fff3;
}

#load-edit-btn:hover {
  background: linear-gradient(120deg, #43a047, #4caf50);
  transform: translateY(-3px);
  box-shadow: 0 6px 16px #4caf5066, inset 0 1px #ffffff4d;
}

#load-edit-btn:active {
  transform: translateY(-1px);
  box-shadow: 0 2px 8px #4caf5066, inset 0 1px #fff3;
}

#current-edit-id {
  color: #666;
  margin-top: 10px;
  font-size: 14px;
  font-weight: 500;
  transition: all .3s;
}

#current-edit-id:hover {
  color: #333;
}

#site-title {
  background-color: #ffffffe6;
  border: 1px solid #00000014;
  border-radius: 28px;
  flex: 1;
  min-width: 280px;
  padding: 16px 24px;
  font-size: 18px;
  transition: all .3s;
  box-shadow: inset 0 2px 4px #00000014, inset 0 1px #ffffff80;
}

#site-title:focus {
  border-color: #ff6b9d4d;
  outline: none;
  transform: translateY(-1px);
  box-shadow: inset 0 2px 4px #0000001a, 0 0 0 3px #ff6b9d1a;
}

body.pink #site-title {
  border: 2px solid #ffd6e4;
}

body.pink #site-title:focus {
  border-color: #ff6b9d;
  box-shadow: inset 0 2px 4px #0000001a, 0 0 0 3px #ff6b9d33;
}

body.white #site-title {
  border: 2px solid #ddd;
}

body.white #site-title:focus {
  border-color: #333;
  box-shadow: inset 0 2px 4px #0000001a, 0 0 0 3px #0003;
}

body.blue #site-title {
  border: 2px solid #b3d1f3;
}

body.blue #site-title:focus {
  border-color: #4a90e2;
  box-shadow: inset 0 2px 4px #0000001a, 0 0 0 3px #4a90e233;
}

.color-picker {
  backdrop-filter: blur(8px);
  background-color: #ffffffe6;
  border-radius: 30px;
  gap: 12px;
  padding: 12px;
  display: flex;
  box-shadow: 0 2px 10px #0000001a, inset 0 1px #ffffff80;
}

.color-btn {
  cursor: pointer;
  background-color: #ffffffe6;
  border: 2px solid #0000;
  border-radius: 24px;
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 500;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px #00000014, inset 0 1px #ffffff80;
}

.color-btn:before {
  content: "";
  background: linear-gradient(90deg, #0000, #ffffff4d, #0000);
  width: 100%;
  height: 100%;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.color-btn:hover:before {
  left: 100%;
}

.color-btn.pink {
  color: #ff6b9d;
  background-color: #fff5f8f2;
  border-color: #ff6b9d4d;
}

.color-btn.white {
  color: #333;
  background-color: #fffffff2;
  border-color: #0003;
}

.color-btn.blue {
  color: #4a90e2;
  background-color: #f0f9fff2;
  border-color: #4a90e24d;
}

.color-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 16px #00000026, inset 0 1px #fffc;
}

.color-btn.active {
  font-weight: bold;
  transform: translateY(-3px);
  box-shadow: 0 6px 16px #0003, inset 0 1px #fffc;
}

.color-btn.pink.active {
  color: #fff;
  background: linear-gradient(120deg, #ff6b9d, #ff85b0);
  border-color: #ff6b9d;
}

.color-btn.white.active {
  color: #fff;
  background: linear-gradient(120deg, #333, #666);
  border-color: #333;
}

.color-btn.blue.active {
  color: #fff;
  background: linear-gradient(120deg, #4a90e2, #6aa9f4);
  border-color: #4a90e2;
}

