  cursor: pointer;
  border: none;
  border-radius: 36px;
  padding: 16px 36px;
  font-size: 18px;
  font-weight: bold;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 12px #00000026, inset 0 1px #fff3;
}

.btn-primary:before {
  content: "";
  background: linear-gradient(90deg, #0000, #ffffff4d, #0000);
  width: 100%;
  height: 100%;
  transition: left .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 16px #0003, inset 0 1px #ffffff4d;
}

.btn-primary:hover:before {
  left: 100%;
}

.btn-primary:active {
  transform: translateY(-1px);
  box-shadow: 0 2px 8px #0003, inset 0 1px #fff3;
}

.btn-secondary {
  color: #333;
