  box-shadow: inset 0 2px 4px #0000001a, 0 0 0 3px #ff6b9d33;
}

body.white #gift-note {
  border: 2px solid #ddd;
}

body.white #gift-note:focus {
  border-color: #333;
  box-shadow: inset 0 2px 4px #0000001a, 0 0 0 3px #0003;
}

body.blue #gift-note {
  border: 2px solid #b3d1f3;
}

body.blue #gift-note:focus {
  border-color: #4a90e2;
  box-shadow: inset 0 2px 4px #0000001a, 0 0 0 3px #4a90e233;
}

@media (width <= 768px) {
  .container {
    border-radius: 15px;
    margin: 10px;
    padding: 15px;
  }

  header h1 {
    font-size: 28px;
  }

  .header-actions {
    flex-direction: column;
    align-items: stretch;
    gap: 15px;
  }

  #site-title {
    width: 100%;
    min-width: unset;
  }

  .color-picker {
    justify-content: center;
  }

  section {
    border-radius: 15px;
    padding: 20px;
  }

  section h2 {
    font-size: 20px;
  }

  .gift-grid {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    gap: 20px;
    padding: 20px;
  }

  .gift-item {
    border-radius: 12px;
  }

  .gift-item img, .gift-item video {
    height: 150px;
  }

  .music-player {
    flex-direction: column;
    align-items: stretch;
    gap: 15px;
    padding: 15px;
  }

  .generate-section .btn-container {
    flex-direction: column;
    align-items: stretch;
  }

  .modal-content {
    border-radius: 20px;
    margin: 15% auto;
    padding: 20px;
  }

  #gift-note {
    height: 100px;
  }
}

@media (width <= 480px) {
  header h1 {
    font-size: 24px;
  }

  .header-actions {
    flex-direction: column;
    align-items: stretch;
  }

  #site-title {
    padding: 10px 15px;
    font-size: 16px;
  }

  .color-btn {
    padding: 8px 16px;
    font-size: 13px;
  }

  section h2 {
    font-size: 18px;
  }

  .gift-grid {
    grid-template-columns: 1fr;
  }

  .btn-primary, .btn-secondary {
    padding: 12px 24px;
    font-size: 16px;
  }

  .modal-content {
    margin: 20% auto;
    padding: 15px;
  }
}

.crop-container {
  border: 1px solid #ddd;
  border-radius: 8px;
  width: 100%;
  max-width: 600px;
  height: 400px;
  margin: 20px auto;
  position: relative;
  overflow: hidden;
}

#crop-image {
  max-width: 100%;
  max-height: 100%;
}

.crop-actions {
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.sortable-ghost {
  opacity: .5;
  background-color: #f0f0f0;
  border: 2px dashed #ff6b9d;
}

.sortable-chosen {
  background-color: #e3f2fd;
  border-color: #4a90e2;
  box-shadow: 0 4px 8px #0003;
}

.sortable-drag {
  opacity: .8;
  transform: rotate(2deg);
  box-shadow: 0 4px 12px #0000004d;
}
